'use strict';

import React, { Component } from 'react';

import StoreReceiptItem from '../component/StoreReceiptItem';
import Util from '../lib/Util';
import axios from '../lib/Axios';
// import { scale, verticalScale, moderateScale } from 'react-native-size-matters';
// import FontWeights from '@lib/fontWeights';

// import * as appKeys from '~/AppKeys';
// import Util from '@lib/Util';
// import * as GlobalKeys from '~/GlobalKeys';

//redux START
// import * as actions from '@actions/index';
// import { connect } from 'react-redux';
//redux END
// import Colors from '@lib/colors';
// import Types from '~/Types';



class StoreDetailReceipt extends Component{

  constructor(props){
    super(props);

    this.state = {
      store_order_id: null,
      store_item_id: null,

      name: '',
      contact: '',
      email: '',

      item_title: '',

      item_price: 0,
      total_price: 0,

      refundButtonText: '',

      isRefund: false,
      refundPolicyText: '',

      is_owner: false
    }
  };

  // shouldComponentUpdate(nextProps: any, nextState: any) {
  //   return true;
  // }

  componentDidMount(){
    const storeOrderIDDom = document.querySelector('#store_order_id');
    if(storeOrderIDDom){
      this.setState({
        store_order_id: Number(storeOrderIDDom.value)
      }, function(){
        //  
        axios.post("/orders/store/owner/check", {
          store_order_id: this.state.store_order_id
        }, (result) => {
          this.setState({
            is_owner: true
          }, () => {
            this.requestOrderInfo();
          })
          
        }, (error) => {

        })
      })
    }
  };

  componentWillUnmount(){
    
  };

  componentDidUpdate(){
  }

  requestOrderInfo(){
    axios.post('/orders/store/info', {
      store_order_id: this.state.store_order_id
    }, (result) => {
      const data = {
        ...result.data
      }

      this.setState({
        contact: data.contact, 
        email: data.email, 
        name: data.name,
        item_title: data.item_title,
        item_price: data.item_price,
        total_price: data.total_price,

        refundButtonText: data.refundButtonText,

        isRefund: data.isRefund,
        refundPolicyText: data.refundPolicyText,

        store_item_id: data.item_id
      }, () => {
      })
    }, (error) => {

    })
  }

  clickCancelOrder(e){
    e.preventDefault();

    swal(" ?", {
      buttons: {
        nosave: {
          text: "",
          value: "notsave",
        },
        save: {
          text: "",
          value: "cancel",
        },
      },
    })
    .then((value) => {
      switch (value) {
        case "cancel":
        {
          this.requestOrderCancel();
        }
        break;
      }
    });    
  }

  requestOrderCancel(){
    showLoadingPopup(' ..');

    axios.post("/orders/store/cancel", {
      store_order_id: this.state.store_order_id
    }, (result) => {

      axios.post("/store/item/order/quantity", {
        item_id: this.state.store_item_id
      }, (result_quantity) => {
        this.successCancelPopup();
      }, (error_quantity) => {
        this.successCancelPopup();
      })

    }, (error) => {
      stopLoadingPopup();
      alert(" ");
    })
  }

  successCancelPopup(){
    stopLoadingPopup();
    swal("  !", 'success').then(() => {
      window.location.reload();
    })
  }

  render(){
    if(!this.state.is_owner){
      return <>   .</>;
    }

    let storeReceiptItemDom = <></>;
    if(this.state.store_order_id){
      storeReceiptItemDom = <StoreReceiptItem store_order_id={this.state.store_order_id} isGoDetailButton={false}></StoreReceiptItem>
    }

    let isRefundDisable = true;
    let refundBackgroundColor = '#cccccc';
    if(this.state.isRefund){
      isRefundDisable = false; 
      refundBackgroundColor = '#ffffff';
    }

    return(
      <div className={'StoreDetailReceipt'}>
        <div className={'page_title'}>
           
        </div>
        <div className={'page_sub_title'}>
          
        </div>
        <div className={'container_box'}>
          {storeReceiptItemDom}
        </div>

        <div className={'container_box_label'}>
          
        </div>

        <div className={'container_box'}>
          <div className={'pay_info_container'}>
            <div className={'pay_info_label'}>
              {this.state.item_title}
            </div>
            <div className={'pay_info_value'}>
              {Util.getNumberWithCommas(this.state.item_price)}
            </div>

            
          </div>

          <div className={'pay_info_under_line'}>
          </div>

          <div className={'pay_info_total_price'}>
            <div style={{display: "flex", justifyContent: 'space-between'}}>
              <div>
                  
              </div>
              <div>
                {Util.getNumberWithCommas(this.state.total_price)}
              </div>
            </div>
          </div>
        </div>

        <button className={'refund_button'} onClick={(e) => {this.clickCancelOrder(e)}} disabled={isRefundDisable} style={{backgroundColor: refundBackgroundColor}}>
          {this.state.refundButtonText}
        </button>

        <div className={'policy_container'}>
          <div className={'policy_title'}>
             / 
          </div>
          <div className={'policy_content'}>
            {this.state.refundPolicyText}
          </div>
        </div>

        <div className={'container_box_label'}>
           
        </div>
        <div className={'container_box'}>
          <div className={'input_label'} style={{marginTop: 0}}></div>
          <input className={'input_box'} disabled={true} type="name" name={'name'} placeholder={' '} value={this.state.name} onChange={(e) => {this.onChangeInput(e, INPUT_STORE_ORDER_NAME)}}/>

          <div className={'input_label'}></div>
          <input className={'input_box'} disabled={true} type="tel" name={'tel'} placeholder={' '} value={this.state.contact} onChange={(e) => {this.onChangeInput(e, INPUT_STORE_ORDER_CONTACT)}}/>

          <div className={'input_label'}></div>
          <input className={'input_box'} disabled={true} type="email" name={'email'} placeholder={' '} value={this.state.email} onChange={(e) => {this.onChangeInput(e, INPUT_STORE_ORDER_EMAIL)}}/>
          
        </div>

      </div>
    )
  }
};

export default StoreDetailReceipt;